import i18n from 'plugins/i18n'

export function getLocaleDtFromTs(ts: number | string, locale: string = i18n.language) {
    if (typeof(ts) === 'string') {
        ts = parseInt(ts)
    }
    const newLocale = locale.replace('_', '-')
    const dt = new Date(ts).toLocaleString(newLocale)
    return dt
}

export function getLocaleDt(dt?: Date, locale: string = i18n.language) {
    if (dt) {
        const newLocale = locale.replace('_', '-')
        return dt.toLocaleString(newLocale)
    }
    return ''
}
