import { useSelector } from 'react-redux'

import { RootState } from 'stores'

import { AccountStatus } from 'stores/reducers/account_reducer'

export function useAccount() {

    const account = useSelector((state: RootState) => state.account)

    const isLogined = account.status === AccountStatus.Logined

    return {
        account: account, 
        isLogined: isLogined
    }

}
