import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export enum AccountStatus {
  Unknown,
  Logined,
}

export interface AccountInfo {
  status: AccountStatus
  accountId: number
  apiKey: string
  eddsaKey: string
  ecdsaKey: string
}

const defaultAccId = process.env.REACT_APP_TEST_ACCOUND_ID ? parseInt(process.env.REACT_APP_TEST_ACCOUND_ID) : 0
const defaultApiKey = process.env.REACT_APP_TEST_API_KEY ? process.env.REACT_APP_TEST_API_KEY : ''
const defaultEddsaKey = process.env.REACT_APP_TEST_EDDSA_KEY ? process.env.REACT_APP_TEST_EDDSA_KEY : ''
const defaultEcdsaKey = process.env.REACT_APP_TEST_ECDSA_KEY ? process.env.REACT_APP_TEST_ECDSA_KEY : ''

const initialState = { 
  status: AccountStatus.Unknown, 
  apiKey: defaultApiKey, 
  eddsaKey: defaultEddsaKey, 
  ecdsaKey: defaultEcdsaKey, 
  accountId: defaultAccId
} as AccountInfo

const accountSlice = createSlice({
  name: 'account',
  initialState,
  reducers: {
    setAccountStatus(state, action: PayloadAction<any>) {
      state.status = action.payload
    },
    setAccountId(state, action: PayloadAction<any>) {
      state.accountId = typeof(action.payload) == 'string' ? parseInt(action.payload): action.payload
    },
    setApiKey(state, action: PayloadAction<any>) {
      state.apiKey = action.payload
    },
    setEddsaKey(state, action: PayloadAction<any>) {
      state.eddsaKey = action.payload
    },
    setEcdsaKey(state, action: PayloadAction<any>) {
      state.ecdsaKey = action.payload
    },
  },
})

export const { setAccountStatus, setApiKey, setAccountId, setEddsaKey, setEcdsaKey, } = accountSlice.actions
export default accountSlice
