import React, { lazy } from 'react'

import {
  HashRouter as Router,
  Switch,
  Route,
  useParams,
  useRouteMatch,
} from 'react-router-dom'

import styled from 'styled-components/macro'
import styles from './routers.module.scss'

import Header from 'layouts/header'
import Footer from 'layouts/footer'

/*
const QuotePanel = lazy(() => import('../pages/QuotePanel'))
const TradingPanel = lazy(() => import('../pages/TradingPanel'))
const LiquidityPanel = lazy(() => import('../pages/LiquidityPanel'))
const L2WalletPanel = lazy(() => import('../pages/L2WalletPanel'))
const WalletShowCase = lazy(() => import('../pages/demo_page/wallet_showcase'))
*/

import HomePage from 'pages/HomePage'

import QuotePage from 'pages/QuotePage'
import TradingPage from 'pages/TradingPage'
import LiquidityPage from 'pages/LiquidityPage'
import L2WalletPage from 'pages/L2WalletPage'

import WalletShowCase from 'pages/DemoPage/wallet_showcase'

const Container = styled.div`
width: 100%;
background: ${({ theme }) => theme.colors.bodyBgColor};
color: #ffffff;
}
`

class RouterView extends React.Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route exact path='/'>
            <Header />
            <Container>
              <div className={styles.content_container}>
                <HomePage />
              </div>
            </Container>
            <Footer />
          </Route>
          <Route exact path='/quote'>
            <Header />
            <Container>
              <div className={styles.content_container}>
                <QuotePage />
              </div>
            </Container>
            <Footer />
          </Route>
          <Route exact path='/trading/quick'>
            <Header />
            <Container>
              <div className={styles.content_container}>
                <TradingPage />
              </div>
            </Container>
            <Footer />
          </Route>
          <Route exact path='/trading/pro'>
            <Header />
            <Container>
              <TradingPage />
            </Container>
            <Footer />
          </Route>
          <Route exact path='/liquidity'>
            <Header />
            <Container>
              <div className={styles.content_container}>
                <LiquidityPage />
              </div>
            </Container>
            <Footer />
          </Route>
          <Route exact path='/l2wallet'>
            <Header />
            <Container>
              <div className={styles.content_container}>
                <L2WalletPage />
              </div>
            </Container>
            <Footer />
          </Route>
          <Route exact path='/demo'>
            <Header />
            <Container>
              <div className={styles.content_container}>
                <WalletShowCase />
              </div>
            </Container>
            <Footer />
          </Route>
        </Switch>
      </Router>
    );
  }
}

export default RouterView;
